
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
    display: inline-block;
    vertical-align: top;
    padding: 6px;
    color: #00a0dc;
    transition: all 0.4s linear;
}
.cartcontrol .cart-decrease .inner {
      transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.move-transition {
      opacity: 1;
      transform: translate3d(0, 0, 0);
}
.cartcontrol .cart-decrease.move-transition .inner {
        display: inline-block;
        line-height: 24px;
        font-size: 24px;
        color: #00a0dc;
        transform: rotate(0);
}
.cartcontrol .cart-decrease.move-enter, .cartcontrol .cart-decrease.move-leave-active {
      opacity: 0;
      transform: translate3d(24px, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .inner, .cartcontrol .cart-decrease.move-leave-active .inner {
        transform: rotate(180deg);
}
.cartcontrol .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: #93999f;
}
.cartcontrol .cart-add {
    display: inline-block;
    vertical-align: top;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0dc;
}



