
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background: rgba(7, 17, 27, 0.5);
}
.header .content-wrapper {
    position: relative;
    padding: 24px 12px 18px 24px;
    font-size: 0;
}
.header .content-wrapper .avatar {
      display: inline-block;
      vertical-align: top;
}
.header .content-wrapper .avatar img {
        border-radius: 2px;
}
.header .content-wrapper .content {
      display: inline-block;
      margin-left: 16px;
}
.header .content-wrapper .content .title {
        margin: 2px 0px 8px 0px;
}
.header .content-wrapper .content .title .brand {
          display: inline-block;
          vertical-align: top;
          width: 30px;
          height: 18px;
          background-image: url("brand@2x.png");
          background-size: 30px 18px;
          background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
              background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
          line-height: 18px;
          margin-left: 6px;
          font-size: 18px;
          font-weight: bold;
}
.header .content-wrapper .content .description {
        margin-bottom: 10px;
        line-height: 12px;
        font-size: 12px;
}
.header .content-wrapper .content .support .text {
        line-height: 12px;
        font-size: 12px;
}
.header .content-wrapper .support-count {
      position: absolute;
      right: 12px;
      bottom: 14px;
      height: 24px;
      line-height: 24px;
      border-radius: 14px;
      background: rgba(0, 0, 0, 0.2);
      padding: 0 8px;
}
.header .content-wrapper .support-count .count {
        vertical-align: top;
        font-size: 11px;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
        margin-left: 2px;
        font-size: 12px;
        line-height: 24px;
}
.header .bulletin-wrapper {
    position: relative;
    height: 28px;
    line-height: 28px;
    padding: 0 22px 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: rgba(7, 17, 27, 0.2);
}
.header .bulletin-wrapper .bulletin-title {
      display: inline-block;
      vertical-align: baseline;
      width: 22px;
      height: 12px;
      background-image: url("bulletin@2x.png");
      background-size: 22px 12px;
      background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-wrapper .bulletin-title {
          background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
      vertical-align: top;
      margin: 0 4px;
      font-size: 10px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
      position: absolute;
      font-size: 10px;
      right: 12px;
      top: 8px;
}
.header .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    -webkit-filter: blur(10px);
            filter: blur(10px);
}
.header .detail {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 112;
    width: 100%;
    height: 100%;
    overflow: auto;
    background: rgba(7, 17, 27, 0.8);
    transition: all 0.5s;
}
.header .detail.fade-enter-active, .header .detail.fade-leave-active {
      opacity: 1;
      background: rgba(7, 17, 27, 0.8);
}
.header .detail.fade-enter, .header .detail.fade-leave-active {
      opacity: 0;
      background: rgba(7, 17, 27, 0);
}
.header .detail .detail-wrapper {
      min-height: 100%;
      width: 100%;
}
.header .detail .detail-wrapper .detail-main {
        margin-top: 64px;
        padding-bottom: 64px;
}
.header .detail .detail-wrapper .detail-main .name {
          line-height: 16px;
          text-align: center;
          font-size: 16px;
          font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .star-warpper {
          margin-top: 18px;
          padding: 2px 0;
          text-align: center;
}
.header .detail .detail-wrapper .detail-main .title {
          display: -ms-flexbox;
          display: flex;
          width: 80%;
          margin: 28px auto 24px auto;
}
.header .detail .detail-wrapper .detail-main .title .line {
            -ms-flex: 1;
                flex: 1;
            position: relative;
            bottom: 6px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.header .detail .detail-wrapper .detail-main .title .text {
            padding: 0 12px;
            font-weight: 700;
            font-size: 14px;
}
.header .detail .detail-wrapper .detail-main .supports {
          width: 80%;
          margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .supports .support-item {
            padding: 0 12px;
            margin-bottom: 12px;
            font-size: 0;
}
.header .detail .detail-wrapper .detail-main .supports .support-item:last-child {
              margin-bottom: 0;
}
.header .detail .detail-wrapper .detail-main .supports .support-item .text {
              line-height: 12px;
              font-size: 12px;
}
.header .detail .detail-wrapper .detail-main .bulletin {
          width: 80%;
          margin: 0 auto;
}
.header .detail .detail-wrapper .detail-main .bulletin .content {
            padding: 0 12px;
            line-height: 24px;
            font-size: 12px;
}
.header .detail .detail-close {
      position: relative;
      width: 32px;
      height: 32px;
      margin: -64px auto 0 auto;
      clear: both;
      font-size: 32px;
}



