
.goods {
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  position: absolute;
  top: 174px;
  width: 100%;
  bottom: 46px;
}
.goods .menu-wrapper {
    -ms-flex: 0 0 80px;
        flex: 0 0 80px;
    width: 80px;
    background: #f3f5f7;
}
.goods .menu-wrapper .menu-item {
      display: table;
      height: 54px;
      width: 56px;
      line-height: 14px;
      padding: 0 12px;
}
.goods .menu-wrapper .menu-item.current {
        position: relative;
        z-index: 11;
        margin-top: -1px;
        background: #fff;
        font-weight: 700;
}
.goods .menu-wrapper .menu-item .text {
        font-size: 12px;
        display: table-cell;
        width: 56px;
        text-align: center;
        position: relative;
        vertical-align: middle;
}
.goods .menu-wrapper .menu-item .text:after {
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          border-top: 1px solid rgba(7, 17, 27, 0.1);
          content: ' ';
}
.goods .menu-wrapper .menu-item:last-child:after, .goods .menu-wrapper .menu-item.current .text:after {
        border-top: 0;
}
.goods .foods-wrapper {
    -ms-flex: 1;
        flex: 1;
}
.goods .foods-wrapper .title {
      padding-left: 14px;
      height: 24px;
      line-height: 24px;
      border-left: 2px solid #d9dde1;
      font-size: 12px;
      color: #93999f;
      background: #f3f5f7;
}
.goods .foods-wrapper .food-item {
      display: -ms-flexbox;
      display: flex;
      margin: 18px;
      padding-bottom: 18px;
      position: relative;
}
.goods .foods-wrapper .food-item:after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        border-top: 1px solid rgba(7, 17, 27, 0.2);
        content: ' ';
}
.goods .foods-wrapper .food-item:last-child {
        margin-bottom: 0px;
}
.goods .foods-wrapper .food-item:last-child:after {
          border-top: 0;
}
.goods .foods-wrapper .food-item .icon {
        -ms-flex: 0 0 57px;
            flex: 0 0 57px;
        margin-right: 10px;
}
.goods .foods-wrapper .food-item .content {
        -ms-flex: 1;
            flex: 1;
        font-size: 0;
}
.goods .foods-wrapper .food-item .content .name {
          margin: 2px 0 8px 0;
          height: 14px;
          line-height: 14px;
          font-size: 14px;
          color: #07111b;
}
.goods .foods-wrapper .food-item .content .desc, .goods .foods-wrapper .food-item .content .extra {
          line-height: 10px;
          font-size: 10px;
          color: #93999f;
}
.goods .foods-wrapper .food-item .content .desc {
          line-height: 12px;
          margin-bottom: 8px;
}
.goods .foods-wrapper .food-item .content .extra .count {
          margin-right: 6px;
}
.goods .foods-wrapper .food-item .content .price {
          font-weight: 700;
          line-height: 24px;
}
.goods .foods-wrapper .food-item .content .price .now {
            margin-right: 8px;
            font-size: 14px;
            color: #f01414;
}
.goods .foods-wrapper .food-item .content .price .old {
            text-decoration: line-through;
            font-size: 10px;
            color: #93999f;
}
.goods .foods-wrapper .food-item .content .cartcontrol-wrapper {
          position: absolute;
          right: 0;
          bottom: 12px;
}



