
#app .tab {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
}
#app .tab:after {
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    content: ' ';
}
#app .tab .tab-item {
    -ms-flex: 1;
        flex: 1;
    text-align: center;
}
#app .tab .tab-item a {
      display: block;
      font-size: 14px;
      color: #4d555d;
}
#app .tab .tab-item a.active {
        color: #f01414;
}



